.drag-file-input {
  height: 100px;
  cursor: pointer; }
  .drag-file-input input {
    height: 100%;
    cursor: pointer; }
    .drag-file-input input.hover + label {
      border-color: #333; }
  .drag-file-input label {
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    transition: border-color .3s;
    cursor: pointer; }
    .drag-file-input label > span {
      display: inline-block;
      width: 100%; }
  .drag-file-input label::after {
    content: none !important; }
